<template>
  <div>
    <Header name="查看物流"></Header>
    <div v-if="data && data.length > 0">
    <van-steps direction="vertical" :active="active" inactive-color="#07c160">
      <van-step v-for="item in data" :key="item.time">
        <p>{{item.time}}</p>
        <h3>{{item.context}}</h3>
      </van-step>
    </van-steps>
    </div>
    <div v-if="isOk" style="background: #fff">
      <van-empty
        class="custom-image"
        image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
        description="暂无数据"
      />
    </div>
    <van-loading size="24px" vertical type="spinner" v-if="!isOk && data.length === 0">加载中...</van-loading>
  </div>
</template>

<script>
import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'
export default {
  components :{
    Header
  },
  data() {
    return {
      active: "0",
      data:'',
      isOk:false,
    };
  },
  mounted() {
    this.show();
  },
  methods: {
    show () {
        let me = this
        let data = JSON.parse(me.$route.query.sw)
        api.kuaidiQuery(data).then(res => {
          if (res.code === 200) {
            if (res.data.data && res.data.data.length > 0) {
              me.data = res.data.data.sort(function(a,b){
                return new Date(a.time) - new Date(b.time);
              })
              me.data.reverse();
            } else {
              me.isOk = true
              me.$notify({
                message: res.data.message,
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
          me.loading = false
        })
      },
  },
};
</script>

<style lang="stylus" scoped>
>>>.van-loading--vertical{
  position absolute
  top calc(50% - 23px)
  left calc(50% - 27px)
}
</style>


